<template>
  <div class="detail">
    <div v-if="bool">
      <!-- <HomeHeader > {{ goback }}</HomeHeader> -->
      <HomeHeader :myBook="bookDetail">{{ goback }}</HomeHeader>

      <van-card
        v-if="bookDetailData._id"
        :thumb="decodeURIComponent(bookDetailData.cover.slice(7))"
      >
        <template #title>
          <h3>{{ bookDetailData.title }}</h3>
        </template>
        <template #desc>
          <span style="color: #b83320">{{ bookDetailData.author }}</span> |
          <span>{{ bookDetailData.minorCate }}</span> |
          <span>{{
            parseInt(bookDetailData.totalFollower / 10000) + "万字"
          }}</span>
          <p>{{ bookDetailData.isSerial ? "完结" : "连载" }}</p>
        </template>
        <!-- <template #price>
        
      </template> -->
      </van-card>
      <div class="detail-botton">
        <!-- type="primary" -->
        <van-button
          :plain="!isAddBook"
          :color="isAddBook ? '#999' : '#b83320'"
          :class="{ subtract: isAddBook }"
          @click="addBook"
          >{{ isAddBook ? "撤出书架" : "加入书架" }}</van-button
        >
        <van-button @click="playBook" type="danger" color="#b83320"
          >开始阅读</van-button
        >
      </div>
      <div v-if="bookDetailData.allowFree" style="text-align: center">
        开通vip,免费阅读此书
      </div>
    </div>
    <!-- 可修改文案和加载图标的颜色 -->
    <van-loading color="#0094ff" v-else class="loading" />
  </div>
</template>

<script>
import HomeHeader from "@/components/HomeHeader.vue";
// import { mapMutation } from "vuex";
export default {
  data() {
    return {
      bookDetail: "书籍详情",
      goback: "⮜ 返回",
      bookDetailData: {},
      bool: false,
      isAddBook: false,
      books: [],
    };
  },
  computed: {
    // ...mapMutation[{ updateChapter }],
  },
  created() {
    this.bool = false;
    // console.log(this.$route.query.id);
    this.axios
      .get("http://novel.kele8.cn/book-info/" + this.$route.query.id)
      .then((result) => {
        // console.log(result);
        this.bool = true;
        this.bookDetailData = result.data;
      });

    this.books = JSON.parse(window.localStorage.getItem("books")) || [];
    this.isAddBook = this.books.some((book) => {
      return book._id == this.$route.query.id;
    });
    // http://novel.kele8.cn/book-chapters/5ae182088ab51e366a5aa7e4
  },
  components: {
    HomeHeader,
    // BookList,
  },
  methods: {
    playBook() {
      this.bool = false;
      this.axios
        .get(
          "http://novel.kele8.cn/book-sources?view=summary&book=" +
            this.$route.query.id
        )
        .then((result) => {
          console.log(result);
          this.axios
            .get("http://novel.kele8.cn/book-chapters/" + result.data[0]._id)
            .then((res) => {
              console.log(this.$route);
              this.$router.push({
                path: "/book/" + this.$route.query.id,
                params: {
                  id: this.$route.query.id,
                },
              });
              // this.bool=false
              // this.$router.push("/book/" + this.$route.query.id);
              this.$store.commit("updateChapter", res.data);
              this.$store.commit("updateBookDetailData", this.bookDetailData);
              // this.bool = true;
            });
        });
    },
    addBook() {
      if (this.isAddBook) {
        this.isAddBook = false;
        console.log(
          this.books.filter((book) => {
            return book._id !== this.bookDetailData._id;
          })
        );
        this.books = this.books.filter((book) => {
          return book._id !== this.bookDetailData._id;
        });
        console.dir(JSON.stringify(this.books));
        window.localStorage.setItem("books", JSON.stringify(this.books));
        return;
      }
      this.books.unshift(this.bookDetailData);
      window.localStorage.setItem("books", JSON.stringify(this.books));
      this.isAddBook = true;
    },
  },
};
</script>

<style lang="less">
.detail {
  overflow: hidden;
  .loading {
    display: flex;
    height: 100vh;
    width: 100vw;
    justify-content: center;
    align-items: center;
  }
  .van-card {
    background-color: #fff;
  }
  .van-card__content {
    // border-bottom: 1px solid #ccc;
    padding-bottom: 5px;
    span:nth-child(-n + 2) {
      padding-right: 5px;
    }
    span:nth-child(n + 3) {
      padding: 0 5px;
    }
  }
  h3 {
    margin: 0 0 5px 0;
    font-weight: 400;
    font-size: 16px;
  }
  .van-card__header {
    margin: 0;
    background-color: white;
    .van-card__thumb {
      width: 65px;
    }
    .van-card__thumb img {
      border-radius: 0;
    }
  }

  .delete-button {
    height: 100%;
  }
  .detail-botton {
    padding: 0 16px;
    display: flex;
    justify-content: space-between;
    // flex-direction: ;
    .van-button {
      // flex: 1;
      width: 45%;
    }
  }
}
</style>